import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
    state,
} from '@umetrics/sartorius-ui-base';
import { NewSelectOptionProperties, NewSelectOptionSlot } from './newSelectOptionProperties';
import { version } from './metadata';
import { NewSelectOptionEvent } from './newSelectOptionEvent';
import { selectOptionStyles } from './newSelectOptionStyle';

@defineElement({ tagName: 'new-select-option', styles: selectOptionStyles, version })
export class NewSelectOption extends BaseElement implements NewSelectOptionProperties {
    // Static members

    // Private members

    // Protected members

    // Public members
    @property({ type: 'string' })
    public value;
    @property({ type: 'string' })
    public label;

    // Parent decide who is selected
    @property({ type: Boolean, converter: booleanConverter })
    public get selected() {
        return this._selected;
    }
    public set selected(val) {
        if (val === this._selected) return;
        if (val === false) this.removeAttribute('selected');
        else {
            this.setAttribute('selected', 'true');
        }
        this._selected = val;
        this.requestUpdate();
    }

    @property({ type: 'boolean', reflect: true, converter: booleanConverter })
    public active;

    @property({ type: Boolean, converter: booleanConverter })
    public disabled;

    @state()
    private _selected;
    @state()
    private _isEllipsis;

    public disconnectedCallback() {
        this._dispatchEvent(NewSelectOptionEvent.NewDeleteOption);
        super.disconnectedCallback();
    }

    protected firstUpdated(_changedProperties) {
        super.firstUpdated(_changedProperties);
        this._dispatchEvent(NewSelectOptionEvent.NewAddOption);
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            disabled: !!this.disabled,
            active: !!this.active,
        });
        const title = this._isEllipsis && this.label ? this.label : '';

        return html`
            <div
                class="${rootClasses}  ${this.selected ? 'selected' : ''}"
                @mouseup=${this.disabled ? null : this._clickHandler}
            >
                <slot name="${NewSelectOptionSlot.ICON_BEFORE}" class="${baseClassName}-icon-before"></slot>
                <div class="${baseClassName}-value" title=${title} @mouseover="${this._isEllipsisActive}">
                    ${this.label ? this.label : this.value}
                </div>
                <slot name="${NewSelectOptionSlot.ICON_AFTER}" class="${baseClassName}-icon-after"></slot>
            </div>
        `;
    }

    private _clickHandler(e) {
        e.preventDefault();
        if (!this.disabled) {
            this._dispatchEvent(NewSelectOptionEvent.NewChange);
        }
    }

    private _isEllipsisActive(e) {
        this._isEllipsis = e.target.offsetWidth < e.target.scrollWidth;
    }

    private _dispatchEvent(event) {
        this.dispatchEvent(
            new CustomEvent(event, {
                detail: {
                    option: this,
                },
                bubbles: true,
                composed: true,
                cancelable: true,
            })
        );
    }
}
