import { NewSelectVariant } from './newSelectProperties';
import {
    baseInputStyle,
    css,
    CSSResultArray,
    cssSelector,
    cssVariableAsVar,
    GenerateStyles,
    unsafeCSS,
} from '@umetrics/sartorius-ui-base-input';

// see https://www.sartorius.com/brand-platform/applications/digital

export const selectStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    const black = cssVariableAsVar('color_primary_3');
    const white = cssVariableAsVar('color_primary_2');
    const red = cssVariableAsVar('color_traffic_light_1');
    const grayThree = cssVariableAsVar('color_secondary_3');
    const grayFour = cssVariableAsVar('color_secondary_4');
    const graySix = cssVariableAsVar('color_secondary_6');
    const graySeven = cssVariableAsVar('color_secondary_7');
    const grayEight = cssVariableAsVar('color_secondary_8');
    const grayTen = cssVariableAsVar('color_secondary_10');

    const expand_duration = cssVariableAsVar('expand_transition_duration');

    return [
        baseInputStyle(tagName),
        css`
            :host {
                display: inline-block;
            }

            :host([full-width]) {
                width: 100%;
            }

            :host([variant=${unsafeCSS(NewSelectVariant.ContainerHeader)}]) {
                background: transparent;
                --select-root-margin-bottom: 0;
                --select-border: 1px solid transparent;
                --select-choice-font-color: ${black};
                --select-min-width: none;
                --select-background: transparent;
                margin-left: -8px;
            }

            :host([variant=${unsafeCSS(NewSelectVariant.ContainerHeader)}]) ${child('head')} {
                justify-content: start;
                gap: 1rem;
                padding-left: 10px;
            }

            :host([variant=${unsafeCSS(NewSelectVariant.ContainerHeader)}]) ${child('label')} {
                color: ${black};
            }

            :host([variant=${unsafeCSS(NewSelectVariant.ContainerHeader)}]) ${child('head')}:hover,
            :host([variant=${unsafeCSS(NewSelectVariant.ContainerHeader)}]) ${child('head')}:focus,
            :host([variant=${unsafeCSS(NewSelectVariant.ContainerHeader)}]) ${child('head')}.open {
                font-weight: bold;
            }

            :host([variant=${unsafeCSS(NewSelectVariant.ContainerHeader)}]) ${child('head')}:hover ${child('icon')},
            :host([variant=${unsafeCSS(NewSelectVariant.ContainerHeader)}]) ${child('head')}:focus ${child('icon')},
            :host([variant=${unsafeCSS(NewSelectVariant.ContainerHeader)}]) ${child('head')}.open ${child('icon')} {
                stroke: black;
                stroke-width: 1px;
            }

            ${root} {
                animation: navigate;
                outline: none;
                max-width: var(--select-max-width, 240px);
                font-size: var(--select-font-size, 14px);
            }

            ${is('small')} ${child('container')} {
                height: var(--select-height, 32px);
            }

            ${is('medium')} ${child('container')} {
                height: var(--textfield-input-height, 40px);
            }

            ${is('large')} ${child('container')} {
                height: var(--select-height, 48px);
            }

            ${is('xlarge')} ${child('container')} {
                height: var(--select-height, 56px);
            }

            ${child('container')} {
                cursor: pointer;
                background-color: var(--select-background, ${white});
                color: ${graySeven};
                outline: none !important;
                min-width: var(--select-min-width, 240px);
                height: var(--select-height, 40px);
                position: relative;
            }

            ::slotted([slot='root-icon']) {
                margin-left: 12px;
                font-size: 16px;
            }

            ${is('extension')} ${child('container-extension')} {
                background: ${cssVariableAsVar('color_secondary_2')};
                display: flex;
                cursor: pointer;
                flex-direction: row;
                justify-content: flex-end;
                align-items: center;
                height: 40px;
                width: 100%;
                padding: 9px 16px;
                border: 1px solid ${cssVariableAsVar('color_secondary_3')};
                border-left: 0;
            }

            ${is('extension')} ${child('container-extension')}:focus-visible {
                outline: 2px dashed black;
                outline-offset: -2px;
            }

            ${child('head')} {
                display: flex;
                justify-content: space-between;
                align-items: center;
                min-height: var(--select-min-height-head, 24px);
                height: 100%;
                padding: var(--select-padding-inner, 8px 12px);
                position: relative;
            }

            ${child('head')}:after {
                content: ' ';
                display: block;
                position: absolute;
                border: var(--select-border, 1px solid ${graySix});
                z-index: 0;
                inset: 0;
                pointer-events: none;
            }

            ${child('head')}:hover:after {
                border: 1px solid #7e7e7e;
                z-index: 1;
            }

            ${child('head')}:focus {
                outline: none;
            }

            ${child('head')}:focus:after,
            ${child('head')}.open:after {
                border: var(--textfield-input-border-focused, 2px solid ${grayTen});
                z-index: 1;
            }

            ${child('label-wrapper')} {
                display: flex;
                margin-bottom: 7px;
                height: 21px;
                justify-content: space-between;
                align-items: center;
            }

            ${child('label-icon')} {
                font-size: 20px;
                color: ${grayEight};
            }

            ${child('label')} {
                color: ${grayEight};
                margin-bottom: 0px;
            }

            ${child('items')} {
                position: relative;
                background: ${white};
                padding: 0;
                margin: 0;
                cursor: pointer;
                z-index: 2;
                user-select: none;
            }

            :host([value='']) ${is('required')} ${child('required-border')}:after {
                border: 1px solid ${red};
                z-index: 1;
            }

            ${child('required-label')} {
                margin-top: 3px;
                color: ${red};
                font-size: 12px;
            }

            ${child('required-label')}.show {
                display: block;
            }

            ${child('required-label')}.hide {
                display: none;
            }

            ${child('feedback-wrapper')} {
                margin-top: 3px;
                position: absolute;
            }

            ${child('feedback-message')} {
                color: ${grayEight} !important;
            }

            ${child('choice')} {
                display: flex;
                justify-content: flex-start;
                align-items: center;
                width: 100%;
                color: var(--select-choice-font-color, ${graySeven});
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                flex-grow: 1;
            }

            ${is('extension')} ${child('choice')} {
                display: flex;
                justify-content: flex-start;
                align-items: center;
                width: 100%;
                color: var(--select-choice-font-color, ${grayEight});
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                flex-grow: 1;
                font-family: Roboto;
                font-size: 12px;
            }

            ${child('value')} {
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }

            ${child('multiple-value')} {
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }

            ${child('multiple-choice')} {
                display: inline-block;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }

            ${child('head')}.open ${child('choice')},
            ${child('head')}:hover ${child('choice')},
            ${child('head')}:focus ${child('choice')} {
                color: var(--select-choice-font-color, ${black});
            }

            ${child('color-line')} {
                position: absolute;
                top: 0px;
                left: 0px;
                width: 4px;
                height: 100%;
            }

            ${child('body')} {
                border: var(--select-options-border, 1px solid ${grayTen});
                position: relative;
                z-index: 3;
                top: -1px;
            }

            ${child('body')}.open {
                visibility: visible;
                transition:
                    visibility ${expand_duration},
                    opacity ${expand_duration} ease-in;
                opacity: 1;
                overflow: auto;
                min-height: 40px;
                background: white;
                width: 100%;
            }

            ${child('body')}.closed {
                visibility: hidden;
                opacity: 0;
            }

            ${child('clear-icon')} {
                color: black;
                font-size: 1.25em;
            }

            /* States */

            ${is('full-width')} {
                max-width: var(--select-max-width, unset);
                width: 100%;
            }

            ${is('disabled')} {
                cursor: not-allowed;
                pointer-events: none;
            }

            ${is('disabled')} ${child('container')} {
                background: #fafafa;
            }

            ${is('disabled')} ${child('choice')} {
                color: var(--select-choice-font-color, ${grayFour});
            }

            ${is('disabled')} ${child('head')}:after {
                border: var(--select-border, 1px solid ${grayThree});
            }

            ${is('disabled', 'extension')} ${child('choice')} {
                color: var(--select-choice-font-color, ${grayFour});
            }

            ${is('disabled')} ${child('label')} {
                color: ${grayFour};
            }

            ${is('disabled')} ${child('label-icon')} {
                color: ${grayFour};
            }

            ${is('disabled')} ${child('feedback-message')} {
                color: ${grayFour} !important;
            }

            ${is('readonly')} {
                cursor: not-allowed;
                pointer-events: none;
            }

            ${is('readonly')} ${child('container')} {
                background: transparent;
            }

            ${is('readonly')} ${child('choice')} {
                color: var(--select-choice-font-color, ${black});
            }

            ${is('readonly')} ${child('head')} {
                color: ${black};
                padding: var(--select-padding-inner, 8px 0px);
            }

            ${is('readonly')} ${child('head')}:after {
                border: var(--select-border, 1px solid ${grayThree});
                color: ${black};
            }

            ${is('readonly')} ${child('label')} {
                color: ${grayEight};
            }

            ${is('readonly')} ${child('label-icon')} {
                color: ${grayFour};
            }

            ${is('readonly')} ${child('feedback-message')} {
                color: ${grayFour} !important;
            }

            ${is('readonly')} ${child('border')}:hover:after {
                border: 0px;
                background-color: transparant;
            }

            ${is('readonly')} ${child('head')}:after {
                border: 0px;
            }

            ${is('readonly', 'clear-button')} ${child('tiny-clear-button')} {
                color: ${grayThree} !important;
            }

            ${is('disabled', 'clear-button')} ${child('tiny-clear-icon')} {
                color: ${grayThree} !important;
            }

            /*NY*/

            ${is('validation')} ${child('head')}:after {
                border: var(--select-border, 1px solid ${red});
            }

            ${is('validation')} ${child('label')} {
                color: ${red};
            }

            ${is('validation')} ${child('label-icon')} {
                color: ${red};
            }

            ${is('validation')} ${child('suffix-icon')} {
                color: ${red};
            }

            ${child('arrow-background')} {
                display: flex;
                justify-content: flex-start;
                align-items: center;
                margin-left: 7px;
            }

            ${child('sort-arrow-background')} {
                display: flex;
                justify-content: flex-start;
                align-items: center;
            }

            /* Gives our opportunity to pass HTML events coming from parent */

            ${child('suffix-icon')} {
                color: black;
            }

            ::slotted([slot='suffix-icon']) {
                font-size: 24px;
                margin-left: 8px;
            }

            ${child('icon')} {
                color: black;
                height: var(--select-arrow-height, 14px);
                display: flex;
                justify-content: flex-start;
                align-items: center;
                position: relative;
                font-size: 1.5em;
            }

            ${child('clear')} {
                cursor: pointer;
            }

            ${is('extension')} ${child('extension-icon')} {
                font-size: 20px;
                color: ${cssVariableAsVar('color_secondary_8')};
            }

            ${is('disabled')} ${child('icon')} {
                color: ${grayFour};
            }

            ${is('disabled', 'extension')} ${child('extension-icon')} {
                color: ${cssVariableAsVar('color_secondary_4')};
            }

            ${is('disabled')} ${child('suffix-icon')} {
                color: ${grayFour};
            }

            ${child('icon')}:after {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                z-index: 1;
            }

            ${child('icon')} > * {
                z-index: 10;
            }

            ${child('search')} {
                font-size: 14px;
                border: none;
                width: 100%;
                flex-grow: 1;
            }

            ${child('search')}:focus {
                outline: none;
            }

            ${child('dropdown')}:focus {
                outline: none;
            }

            ${child('no-options')} {
                font-style: italic;
                color: ${graySeven};
                padding: 10px 16px;
                text-align: center;
            }

            ${is('virtualizer')} ${child('no-options')} {
                position: absolute;
                inset: 0;
                margin: auto;
            }

            ${is('virtualizer')} ${child('li')} {
                width: 100%;
            }
        `,
    ] as CSSResultArray;
};
