import {
    BaseInput,
    booleanConverter,
    createClassName,
    defineElement,
    FeedbackMessageType,
    html,
    property,
    query,
    state,
} from '@umetrics/sartorius-ui-base-input';

import down from '@umetrics/sartorius-ui-icon/dist/icons/down';
import cancel from '@umetrics/sartorius-ui-icon/dist/icons/cancel';
import { selectStyles } from './newSelectStyle';
import { NewSelectOption } from './newSelectOption';
import { NewSelectEvent } from './newSelectEvent';
import { NewSelectGroup } from './newSelectGroup';
import {
    AutoSelect,
    NewSelectProperties,
    NewSelectSize,
    NewSelectSlot,
    NewSelectVariant,
    SelectOption,
    SelectOptions,
} from './newSelectProperties';
import { NewSelectOptionEvent } from './newSelectOptionEvent';
import { version } from './metadata';
import { NewSelectGroupEvent } from './newSelectGroupEvent';
import type { DropdownPlacement } from '@umetrics/sartorius-ui-dropdown';
import { NewSelectOptionSlot } from './newSelectOptionProperties';
import arrowSort from '@umetrics/sartorius-ui-icon/dist/icons/arrowSort';
import { autoSelect, perfDebounce, preventResizeObserverError } from '@umetrics/sartorius-ui-utils';
import '@lit-labs/virtualizer'; // <lit-virtualizer> is a side effect of this import

@defineElement({
    tagName: 'new-select',
    styles: selectStyles,
    version,
})
export class NewSelect extends BaseInput<NewSelect, string | string[]> implements NewSelectProperties {
    // Static members
    private static Icon;
    private static Dropdown;
    private static AnimationVariant;
    private static Button;

    private static animations = {
        slide: {
            showAnimation: [
                {
                    transform: 'translateX(50%)',
                    opacity: '0',
                },
                {
                    transform: 'translateX(0)',
                    opacity: '1',
                },
            ],
            hideAnimation: [
                {
                    transform: 'translateX(0)',
                    opacity: '1',
                },
                {
                    transform: 'translateX(50%)',
                    opacity: '0',
                },
            ],
        },
        hide: {
            fadeOut: [
                {
                    opacity: '0',
                },
                {
                    opacity: '1',
                },
            ],
        },
    };

    // Private members

    // Protected members

    // Public members
    @property({ type: String, reflect: false })
    public placeholder = '';
    @property({ type: Boolean, attribute: 'full-width', reflect: true, converter: booleanConverter })
    public fullWidth = false;
    @property({ type: String, attribute: 'color-line' })
    public colorLine = '';
    @property({ reflect: true })
    public variant? = NewSelectVariant.Default;
    @property({ type: Boolean, converter: booleanConverter })
    public required = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    public multiple = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    public searchable = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter, attribute: 'clear-button' })
    clearButton = false;
    @property({ type: Boolean, converter: booleanConverter, attribute: 'readonly' })
    readOnly = false;

    @property({ type: String, reflect: true })
    get value() {
        return this._value;
    }

    set value(val) {
        const prev = this._value;
        if (prev !== val) {
            this._value = val;
            if (!this.multiple) {
                const source = this.options ? this.options.option : this._options;
                const targetedValue = source.find((el) => el.value === val);
                if (targetedValue) {
                    this.handleMenuOption(targetedValue, true);
                } else {
                    this._checkOptionValue(val);
                }
            } else {
                this._checkMultipleValue(val);
            }
        }
        this.requestUpdate('value', prev);
    }

    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    open = false;
    @property({ type: String, attribute: 'force-placement' })
    forcePlacement?: DropdownPlacement;
    @property({ type: NewSelectSize, reflect: true })
    size = NewSelectSize.MEDIUM;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    extension = false;
    @property({ attribute: 'auto-select' })
    autoSelect?: AutoSelect;
    @property({ type: Object, attribute: false })
    public options?: SelectOptions;

    @query('div')
    private _root;

    @query('[class$="-back"]')
    private _backHtml;
    @query('[class$="-clear"]')
    private _clear;
    @query('[class$="-dropdown"]')
    private _dropdown;
    @query('[class$="-search"]')
    private _input;
    @query('[class$="-value"]')
    private _valueSpan;

    @state()
    private _listStructure: any = [];
    @state()
    private _navigationStructure: any = [];
    @state()
    private _query = '';
    @state()
    private _isEllipsis = false;
    @state()
    private _titleValue = '';

    @state()
    private _backText = '';
    private _slottedElement: any = null;

    private _depthIndex = 0;

    private _options: NewSelectOption[] = [];
    @state()
    private _label: string | string[] = '';
    private _value: string | string[] = '';
    private _debouncedCallback;
    private _scroller;

    constructor() {
        super();
        this._changeOptionEventHandler = this._changeOptionEventHandler.bind(this);
        this._addOptionEventHandler = this._addOptionEventHandler.bind(this);
        this._deleteOptionEventHandler = this._deleteOptionEventHandler.bind(this);
        this._handleKeyDown = this._handleKeyDown.bind(this);
        this._navigate = this._navigate.bind(this);
        this._navigateBack = this._navigateBack.bind(this);
        this._handleStateChange = this._handleStateChange.bind(this);
        this._handleSearch = this._handleSearch.bind(this);
        this._handleSlotChange = this._handleSlotChange.bind(this);
        this._isEllipsisActive = this._isEllipsisActive.bind(this);
        preventResizeObserverError();
    }

    focus(options?: FocusOptions) {
        const head = this.shadowRoot!.querySelector('[class*="-head"]');
        (head as HTMLDivElement).focus(options);
    }

    blur() {
        const head = this.shadowRoot!.querySelector('[class*="-head"]');
        (head as HTMLDivElement).blur();
    }

    public handleMenuOption(option, fromSetter?): void {
        if (this.multiple) {
            this._toggleOption(option);
            this._setMultipleSelected();
        } else {
            this.value = option.value;
            this._label = option.label;

            this._options.forEach((options) => {
                if (options instanceof NewSelectOption) {
                    options.selected = options.value === this.value;
                }
            });
            window.requestAnimationFrame(() => {
                this._addClearToNavigation();
            });
        }

        if (fromSetter) {
            const customEvent = new CustomEvent(NewSelectEvent.NewSelectionChange, {
                bubbles: true,
                composed: true,
                detail: {
                    selected: true,
                    value: this.value,
                },
            });

            this.dispatchEvent(customEvent);
        }
        this.requestUpdate();
    }

    // Public members

    connectedCallback() {
        this.style.setProperty('display', 'none');
        const imports: any[] = [];
        imports.push({ module: () => import('@umetrics/sartorius-ui-button'), elem: 'Button' });
        imports.push({ module: () => import('@umetrics/sartorius-ui-icon'), elem: ['Icon'] });
        imports.push({
            module: () => import('@umetrics/sartorius-ui-dropdown'),
            elem: ['Dropdown', 'AnimationVariant'],
        });
        NewSelect.subRegister(NewSelectGroup);
        NewSelect.subRegister(NewSelectOption);
        NewSelect.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.style.removeProperty('display');
        });

        this.addEventListener(NewSelectOptionEvent.NewChange, this._changeOptionEventHandler);
        this.addEventListener(NewSelectOptionEvent.NewStateChange, this._handleStateChange);
        this.addEventListener(NewSelectOptionEvent.NewAddOption, this._addOptionEventHandler);
        this.addEventListener(NewSelectGroupEvent.NewNavigate, this._navigate);
        this.addEventListener(NewSelectGroupEvent.NewNavigateBack, this._fadeOut);
        this.addEventListener('keydown', this._handleKeyDown);
    }

    disconnectedCallback() {
        this.removeEventListener(NewSelectOptionEvent.NewChange, this._changeOptionEventHandler);
        this.removeEventListener(NewSelectOptionEvent.NewStateChange, this._handleStateChange);
        this.removeEventListener(NewSelectOptionEvent.NewAddOption, this._addOptionEventHandler);
        this.removeEventListener(NewSelectGroupEvent.NewNavigateBack, this._fadeOut);
        this.removeEventListener(NewSelectGroupEvent.NewNavigate, this._navigate);
        this.removeEventListener('keydown', this._handleKeyDown);

        super.disconnectedCallback();
    }

    public toggle(forceToggle = false): void {
        if (!this._shouldToggle(forceToggle)) return;
        if (this.searchable) {
            this._query = '';
            this._onQueryUpdate();
        }

        this.open = !this.open;
        if (this.open && this.searchable) {
            window.requestAnimationFrame(() => {
                this._addClearToNavigation();

                if (this.open) this._input.focus();
                this._navigationStructure.unshift(this._input);
            });
        } else {
            window.requestAnimationFrame(() => {
                this._addClearToNavigation();
            });
        }
        this._dispatchToggle();
    }

    protected firstUpdated(_changedProperties): void {
        this._slottedElement = this.shadowRoot!.querySelector('slot:not([name])');
        this._slottedElement?.addEventListener('slotchange', this._handleSlotChange);
        this._dropdown.updateComplete.then(() => {
            this._scroller = this._dropdown.shadowRoot.querySelector('.container');
        });
    }

    protected updated(_changedProperties): void {
        super.updated(_changedProperties);

        if (_changedProperties.has('open')) {
            this.updatedOpen();
        }

        if (_changedProperties.has('_navigationStructure') || _changedProperties.has('autoSelect')) {
            this._slottedElement = this.shadowRoot!.querySelector('slot:not([name])');
            this._slottedElement?.addEventListener('slotchange', this._handleSlotChange);
            this._dropdown.updateComplete.then(() => {
                this._scroller = this._dropdown.shadowRoot.querySelector('.container');
            });

            if (this.autoSelect && this._navigationStructure.length > 0) {
                const options = this.options?.option ?? this._navigationStructure;
                const optionsValues = options.map((o) => o.value);
                const autoSelectedOption = autoSelect(
                    this.autoSelect.matchingKeyWords,
                    optionsValues,
                    this.autoSelect.threshold
                );
                // Dispatch the event only if the selected value has changed
                if (autoSelectedOption !== undefined && this.value !== autoSelectedOption.bestMatch) {
                    const customEvent = new CustomEvent(NewSelectEvent.NewSelectionChange, {
                        bubbles: true,
                        composed: true,
                        detail: {
                            selected: false,
                            value: autoSelectedOption.bestMatch,
                        },
                    });

                    this.value = this.multiple ? [autoSelectedOption.bestMatch] : autoSelectedOption.bestMatch;
                    this.dispatchEvent(customEvent);
                }
            }
        }
    }

    protected updatedOpen() {
        if (this.open && this.options) {
            // Query lit-virtualizer and do scrollIntoView,like : virtualizer.element(index).scrollIntoView({ block: 'nearest', behavior: 'auto' }) does not work.
            // Issues :
            // https://github.com/lit/lit/issues/3963
            // https://github.com/lit/lit/issues/4767
            // Below is a workaround to at least find a decent position
            setTimeout(() => {
                const container = this._dropdown.shadowRoot!.querySelector('[class*="container"]');
                const selected = this.value
                    ? this.options!.option.findIndex((o) => o.value === this.value)
                    : this.options!.scrolledItem;

                container.scrollTop = 36 * Math.max(1, selected ?? 1);
            }, 100);
        }
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            'full-width': this.fullWidth,
            required: this.required,
            disabled: this.disabled,
            readonly: this.readOnly,
            'clear-button': this.clearButton,
            [this.size]: true,
            validation: this.additionalFeedbackType === FeedbackMessageType.Failure,
            extension: this.extension,
            virtualizer: !!this.options,
        });

        const backHtml = html`
            <${NewSelectGroup.tag} back value="${this._backText}"
                                   class="${baseClassName}-back"></${NewSelectGroup.tag}>`;
        const clearHtml = html`
            <${NewSelectOption.tag} value="_clear" label="Clear selection" @click="${this._clearSelection}"
                                    class="${baseClassName}-clear">
                <${NewSelect.Icon.tag}
                    icon=${cancel}
                    slot="${NewSelectOptionSlot.ICON_BEFORE}"
                    class="${baseClassName}-clear-icon"
                >
                </${NewSelect.Icon.tag}>
            </${NewSelectOption.tag}>`;

        const noOptionsHtml = this._noOptionsHtml(baseClassName);
        const selectContainer = this._selectContainer(baseClassName);
        const labelHtml = this._renderLabelWrapper(baseClassName);

        let optionsHtml;
        if (this.options) {
            optionsHtml = html`
                <lit-virtualizer
                    .items=${this._query
                        ? this.options.option.filter((x) => this._testOption(x.label, x.value))
                        : this.options.option}
                    .renderItem=${(v, i) => this._renderVirtualItem(baseClassName, v, i)}
                    .scroller=${false}
                ></lit-virtualizer>
            `;
        } else {
            optionsHtml = html` <slot></slot>`;
        }

        return html`
            <div class="${rootClasses}" ?disabled="${!!this.disabled}" ?required="${this.required}" ?readonly="${
                this.readOnly
            }">
                ${labelHtml}
                ${selectContainer}
                <${NewSelect.Dropdown.tag}
                    tabindex="${this.searchable && this.open ? '0' : ''}"
                    @focus="${() => (this._navigationStructure[0].active = true)}"
                    class='${baseClassName}-dropdown'
                    fixed-width=${this.variant === NewSelectVariant.Default}
                    force-placement="${this.forcePlacement}"
                    open="${this.open}"
                    animation-variant="${NewSelect.AnimationVariant.Select}"
                    @toggle="${this._handleDropdownToggle}"
                    object-id="select-trigger"
                    .parentObject="${this._root}"
                    controlled="true"
                    @close="${(e) => {
                        if (e.srcElement !== this._dropdown) {
                            return;
                        }
                        if (this.searchable) {
                            this._query = '';
                        }
                        this.open = false;
                        this._navigationStructure
                            .filter((option) => !!option)
                            .forEach((option) => (option.active = false));
                        this._dispatchToggle();
                        e.stopPropagation();
                    }}"

                >
                    ${this.clearButton && this._label ? clearHtml : null}
                    ${this._depthIndex !== 0 ? html`${backHtml}` : null}
                    <ul class="${baseClassName}-items">
                        ${optionsHtml}
                        ${noOptionsHtml}
                    </ul>
                </${NewSelect.Dropdown.tag}>
                <div class="${baseClassName}-feedback-wrapper">
                    ${this._renderFeedback(baseClassName, this.additionalFeedbackMessage, this.additionalFeedbackType)}
                </div>
            </div>
        `;
    }

    protected renderArrow(baseClassName) {
        return html`
            <div class="${baseClassName}-arrow-background">
                <${NewSelect.Icon.tag} class="${baseClassName}-icon"
                                       icon=${down}
                                       ?flip-vertical="${this.open}"
                >
                </${NewSelect.Icon.tag}>
            </div>
        `;
    }

    protected renderArrowSort(baseClassName) {
        return html`
            <div class="${baseClassName}-sort-arrow-background">
                <${NewSelect.Icon.tag} class="${baseClassName}-extension-icon"
                                       icon=${arrowSort}
                                       ?flip-vertical="${this.open}"
                >
                </${NewSelect.Icon.tag}>
            </div>
        `;
    }

    protected renderClearButton(baseClassName) {
        return html`
            <div class="${baseClassName}-tiny-clear-button-background">
                <${NewSelect.Button.tag} class="${baseClassName}-tiny-clear-button"
                                         button-size="extra-small"
                                         compact="true"
                                         type="tertiary"
                                         @click="${(e) => {
                                             e.preventDefault();
                                             this._clearSelection();
                                         }}"
                >
                    <${NewSelect.Icon.tag}
                        icon=${cancel}
                        class="${baseClassName}-tiny-clear-icon"
                        slot='icon-slot'
                        size='tiny'
                    >
                    </${NewSelect.Icon.tag}>
                </${NewSelect.Button.tag}>
            </div>
        `;
    }

    private _shouldToggle(forceToggle: boolean): boolean {
        return !(this.multiple && this.open && !forceToggle);
    }

    private _renderLabelWrapper = (baseClassName) =>
        this.label
            ? html` <div class="${baseClassName}-label-wrapper">
                  ${this._renderLabel(baseClassName)}
                  <slot name="${NewSelectSlot.LABEL_ICON}" class="${baseClassName}-label-icon"></slot>
              </div>`
            : null;

    private _renderVirtualItem = (baseClassName, opt: SelectOption, index) => {
        // Only flat option support, in theory presence of opt.option array could yield a group here.
        const lbl = opt.label ?? index;
        const val = opt.value ?? lbl;

        return html`
            <${NewSelectOption.tag} class="${baseClassName}-li" selected='${
                val === this.value || (Array.isArray(this.value) && this.value.includes(val))
            }' @mousedown=${opt.clickCallback} label='${lbl}' value='${val}' icon='${opt.icon}' style=''>
            </${NewSelectOption.tag}>`;
    };

    private _updateNavigationStructure(slotTarget: undefined | HTMLSlotElement = undefined, instant = false) {
        const updateFunc = (target: HTMLSlotElement | undefined) => {
            this._listStructure = [];

            let ele;
            if (target) {
                // Slotted options
                ele = target.assignedElements();
            } else {
                // Virtualized options
                ele = this.shadowRoot!.querySelectorAll('[class$="-li"]');
            }

            ele.forEach((option) => {
                this._listStructure.push(option);
            });
            this._navigationStructure = Array.from(this._listStructure);
            this.requestUpdate('_navigationStructure');
            this._debouncedCallback = undefined;
        };

        if (instant) {
            updateFunc(slotTarget);
        } else {
            // Performance debouncer, do once when browser idle
            if (!this._debouncedCallback) {
                this._debouncedCallback = perfDebounce(updateFunc, 'idle');
            }
            this._debouncedCallback(slotTarget);
        }
    }

    private _handleDropdownToggle(e) {
        e.stopPropagation();
        const regex = /-tiny-clear-button/;
        if (
            e.detail.triggerEvent &&
            e.detail.triggerEvent
                .composedPath()
                .some((el) => el instanceof Element && Array.from(el.classList).some((cls) => regex.test(cls)))
        ) {
            // Press clear button
            e.preventDefault();
            return;
        }
        const forceToggle = true;
        this.toggle(forceToggle);
    }

    private _dispatchToggle() {
        const customEvent = new CustomEvent('toggle', {
            bubbles: true,
            composed: true,
            detail: {
                open: this.open,
                value: this.value,
            },
        });
        this.dispatchEvent(customEvent);
    }

    private _renderArrowIcon(baseClassName) {
        const isNewSelectVariantDefaultArrowIcon =
            this.variant === NewSelectVariant.Default ? this.renderArrow(baseClassName) : null;
        const isNewSelectVariantDefaultSortArrowIcon =
            this.variant === NewSelectVariant.Default ? this.renderArrowSort(baseClassName) : null;
        return !this.extension ? isNewSelectVariantDefaultArrowIcon : isNewSelectVariantDefaultSortArrowIcon;
    }

    private _selectContainer(baseClassName) {
        const searchHtml = this._searchHtml(baseClassName);
        const slotHtml = this._renderSlotHtml(baseClassName, this._renderArrowIcon(baseClassName));
        const clearButton =
            this.clearButton && this.value && !this.readOnly ? this.renderClearButton(baseClassName) : null;

        const colorLine = html` <div
            class="${baseClassName}-color-line"
            style="background-color: ${this.readOnly ? '' : this.colorLine}"
        ></div>`;

        return html` <div class="${baseClassName}${!this.extension ? '-container' : ''}" id="select-trigger">
            <div
                class="${baseClassName}-required-border ${baseClassName}${!this.extension
                    ? '-head'
                    : '-container-extension'} ${!this.open ? '' : 'open'}"
                tabindex=${!this.disabled ? '0' : ''}
            >
                ${!this.extension ? colorLine : null}
                ${!this.extension && this.variant === NewSelectVariant.ContainerHeader
                    ? this.renderArrow(baseClassName)
                    : null}
                ${this.extension && this.variant === NewSelectVariant.ContainerHeader
                    ? this.renderArrowSort(baseClassName)
                    : null}
                ${searchHtml} ${clearButton} ${slotHtml}
            </div>
        </div>`;
    }

    private _renderSlotHtml = (baseClassName, renderArrowIcon) => {
        if (!this.readOnly) {
            return html` <slot name="${NewSelectSlot.ROOT_ICON}" class="${baseClassName}-icon"></slot>
                ${renderArrowIcon}
                <slot name="${NewSelectSlot.SUFFIX_ICON}" class="${baseClassName}-suffix-icon"></slot>`;
        } else return html``;
    };

    private _multipleHtml = (baseClassName) => {
        if (this.multiple) {
            const labelString = this._selectLabelValue();
            return html`<span title="${labelString}" class="${baseClassName}-multiple-value">${labelString}</span>`;
        } else if ((!this._label && !this.value) || this._label === 'Clear selection') {
            return html`${this.placeholder}`;
        } else if (!this.disabled) {
            const title = this._isEllipsis ? this._titleValue : '';
            return html`<span
                class="${baseClassName}-value"
                @mouseover="${this._isEllipsisActive}"
                title="${title}"
                style="${!this.extension ? 'color: black' : ''}"
                >${this._label || this.value}</span
            >`;
        } else {
            return html`${this._label || this.value}`;
        }
    };

    private _selectLabelValue() {
        if (!this._label || this._label.length < 1) return this.placeholder;
        else if (typeof this._label === 'string') return this._label;
        else return (this._label as Array<string>).join(', ');
    }

    private _noOptionsHtml = (baseClassName) => {
        if (this.searchable && this._query !== '') {
            return this._navigationStructure.length === 1
                ? html` <div class="${baseClassName}-no-options">No options</div>`
                : html``;
        } else {
            return this._navigationStructure.length === 0
                ? html` <div class="${baseClassName}-no-options">No options</div> `
                : html``;
        }
    };

    private _searchHtml = (baseClassName) => {
        if (this.searchable && this.open) {
            return html`
                <input
                    @click="${(e) => e.stopPropagation()}"
                    @input="${this._handleSearch}"
                    placeholder="${this._label}"
                    .active="${true}"
                    class="${baseClassName}-search"
                />
            `;
        } else {
            const multipleHtml = this._multipleHtml(baseClassName);
            return html` <div class="${baseClassName}-choice" ${this.readOnly ? 'readonly' : ''}>${multipleHtml}</div>`;
        }
    };

    private _clearSelection() {
        this.value = '';
        this._label = '';

        if (this._clear) {
            this._clear.selected = false;
        }
        const customEvent = new CustomEvent(NewSelectEvent.NewSelectionChange, {
            bubbles: true,
            composed: true,
            detail: {
                selected: false,
                value: this.value,
            },
        });

        this.dispatchEvent(customEvent);
    }

    private _checkOptionValue(val) {
        let hasValue = false;
        this._options.forEach((options) => {
            if (options instanceof NewSelectOption) {
                options.selected = options.value === val;
                if (options.selected) hasValue = true;
            }
        });
        if (!hasValue) this._label = '';
    }

    /**
     * Called from setter
     */
    private _checkMultipleValue(val) {
        const labels: string[] = [];
        if (this.options) {
            this.options.option.forEach((option) => {
                if (val instanceof Array) {
                    val.forEach((el) => {
                        if (option.value === el) {
                            labels.push(option.label);
                        }
                    });
                }
            });
        } else {
            this._options.forEach((options) => {
                options.selected = false;
                if (options instanceof NewSelectOption) {
                    if (val instanceof Array) {
                        val.forEach((el) => {
                            if (options.value === el) {
                                options.selected = true;
                                labels.push(options.label);
                            }
                        });
                    }
                }
            });
        }
        this._label = labels;
    }

    private _addOptionEventHandler(event: Event) {
        event.stopPropagation();
        const evt = event as CustomEvent;
        const element = evt.detail.option as NewSelectOption;

        if (this.options) {
            this._updateNavigationStructure();
        } else if (element.selected) {
            // virtualization will swap elements in and out of dom, so we cant assume reset of selected item
            this._toggleOption(element);
        }
        const index = this._options.indexOf(element);
        if (index === -1) {
            this._options.push(element);
            element.addEventListener(NewSelectOptionEvent.NewDeleteOption, this._deleteOptionEventHandler);
        }
    }

    private _deleteOptionEventHandler(event: Event) {
        event.stopPropagation();
        const evt = event as CustomEvent;
        const element = evt.detail.option as NewSelectOption;
        const index = this._options.indexOf(element);
        this._options.splice(index, 1);

        if (element.selected && !this.options) {
            // virtualization will swap elements in and out of dom, so we cant assume reset of selected item
            this.value = '';
            this._label = '';
        }
    }

    private _setMultipleSelected() {
        this._listStructure.forEach((options) => {
            if (options instanceof NewSelectOption) {
                options.selected = this.value.includes(options.value);
            } else {
                const objectArr = Array.from(options.children);
                objectArr.forEach((element) => {
                    if (element instanceof NewSelectOption) {
                        element.selected = this.value.includes(element.value);
                    }
                });
            }
        });
        const customEvent = new CustomEvent(NewSelectEvent.NewSelectionChange, {
            bubbles: true,
            composed: true,
            detail: {
                selected: false,
                value: this.value,
            },
        });

        this.dispatchEvent(customEvent);
        this.requestUpdate();
    }

    private _fadeIn() {
        const content = this._dropdown.querySelector('[class$="-items"]');
        const animationKeyframes = NewSelect.animations['slide'].showAnimation;
        const animationOptions = {
            duration: 150,
            iterations: 1,
        };
        const fadeInAnimation = content.animate ? content.animate(animationKeyframes, animationOptions) : undefined;
        if (fadeInAnimation) {
            fadeInAnimation.onfinish = () => {};
        }
    }

    private _fadeOut() {
        const content = this._dropdown.querySelector('[class$="-items"]');
        const slideOutKeyframes = NewSelect.animations['slide'].hideAnimation;
        const fadeInKeyframes = NewSelect.animations['hide'].fadeOut;
        const slideOutOptions = { duration: 150, iterations: 1 };
        const fadeInOptions = { duration: 150, iterations: 1 };
        const slideOutAnimation = content.animate ? content.animate(slideOutKeyframes, slideOutOptions) : undefined;
        if (slideOutAnimation) {
            slideOutAnimation.onfinish = () => {
                const fadeInAnimation = content.animate ? content.animate(fadeInKeyframes, fadeInOptions) : undefined;
                this._navigateBack();
                if (fadeInAnimation) {
                    fadeInAnimation.onfinish = () => {};
                }
            };
        } else {
            //For jest
            this._navigateBack();
        }
    }

    private async _navigate(group) {
        this._depthIndex++;
        this._fadeIn();
        let target = group;
        if (group instanceof CustomEvent) {
            target = group.target;
        }
        target.open = true;
        this._listStructure = [];
        const objectArr = Array.from(target.parentElement.children);
        this._listStructure = objectArr;
        this._listStructure.forEach((option) => {
            if (target !== option && (option instanceof NewSelectOption || option instanceof NewSelectGroup)) {
                option.style.display = 'none';
            }
        });
        this._navigationStructure = Array.from(target.children);

        this._hideOptions();

        this.requestUpdate();
        await this.updateComplete;
        const back = this._dropdown.querySelector('[class$="-back"]');

        if (this.searchable) {
            this._navigationStructure.splice(1, 0, back);
        } else {
            this._navigationStructure.unshift(back);
        }
        this._addClearToNavigation();
        if (!(group instanceof CustomEvent)) {
            this._navigationStructure[0].active = true;
        }
        this._backText = target.value;
        this._dropdown.requestUpdate('navigated', 'true');
    }

    private _navigateBack() {
        let temp;
        this._depthIndex--;
        let wasOpenGroup;
        this._listStructure.forEach((option) => {
            option.active = false;
            if (this._backText === option.value && !option.back) {
                option.open = false;
                wasOpenGroup = option;
                temp = option;
                this._backText = option.parentElement.value;
            }
            option.style.display = 'block';
        });
        this._listStructure = [];
        if (temp) {
            this._listStructure = Array.from(temp.parentElement.parentElement.children);
            this._navigationStructure = Array.from(temp.parentElement.children);

            this._hideOptions(wasOpenGroup, true);
            if (this.searchable && this._depthIndex !== 0) this._navigationStructure.splice(1, 0, this._backHtml);
            else if (this._depthIndex !== 0) this._navigationStructure.unshift(this._backHtml);
            this._addClearToNavigation();
        }
        this._dropdown.requestUpdate('navigated', 'true');
        this.requestUpdate();
    }

    private _hideOptions(wasOpenGroup?, back?) {
        if (this.searchable) {
            this._navigationStructure.forEach((el) => {
                this._checkOption(el, wasOpenGroup, back);
            });
        }
    }

    private _checkOption(el, wasOpenGroup?, back?) {
        if (el instanceof NewSelectOption) {
            if (!el.label.toLowerCase().includes(this._query.toLowerCase())) {
                el.style.display = 'none';
            }
        } else if (el instanceof NewSelectGroup && !this._searchGroup(el)) {
            if (back) {
                if (el !== wasOpenGroup) {
                    el.style.display = 'none';
                }
            } else el.style.display = 'none';
        }
    }

    private _toggleOption(option) {
        if (this.multiple) {
            this._toggleOptionsMultiple(option);
        } else {
            this.value = option.value;
            this._label = option.label || option.value;
        }
    }

    private _toggleOptionsMultiple(option) {
        if (!Array.isArray(this.value)) {
            this.value = [];
            this._label = [];
        }
        if (this.value.includes(option.value)) {
            this.value = this.value.filter((x, i) => {
                if (x !== option.value) {
                    return true;
                }
                (this._label as []).splice(i, 1);
                return false;
            });
        } else {
            this.value = [...this.value, option.value];
            if (!this._label.includes(option.label || option.value)) {
                this._label = [...this._label, option.label || option.value];
            }
        }
    }

    private _changeOptionEventHandler(event: Event) {
        event.stopPropagation();
        const evt = event as CustomEvent;
        const element = evt.detail.option as NewSelectOption;

        this.handleMenuOption(element);
        this.toggle();
    }

    private _handleSlotChange(event) {
        this._updateNavigationStructure(event.target);
    }

    private _handleStateChange(event) {
        const evt = event as CustomEvent;
        const element = evt.detail.option as NewSelectOption;
        if (element.selected) {
            this._toggleOption(element);
        }
    }

    private _testOption(label, value) {
        return label !== undefined
            ? label.toLowerCase().includes(this._query.toLowerCase())
            : value.toLowerCase().includes(this._query.toLowerCase());
    }

    private _searchIteration(group: NewSelectGroup, arr) {
        const groupSlot = group.shadowRoot!.querySelector('slot');
        groupSlot?.assignedNodes().forEach((elem) => {
            if (elem instanceof NewSelectOption) arr.push(elem);
            else if (elem instanceof NewSelectGroup) this._searchIteration(elem, arr);
        });
        return arr;
    }

    private _addClearToNavigation() {
        const clear = this._clear;
        const isClearAdded = this._navigationStructure.some((option) => option?.className.includes('-clear'));
        if (clear) {
            if (!isClearAdded) {
                this._navigationStructure.splice(0, 0, clear);
            } else {
                this._navigationStructure[0] = clear;
            }
        } else if (isClearAdded) {
            this._navigationStructure.shift();
        }
    }

    private _isClearOption = (option) => option?.classList === this._clear?.classList;

    private _searchGroup(group) {
        let shouldInclude = false;
        const groupSlot = group.shadowRoot!.querySelector('slot');
        groupSlot?.assignedNodes().forEach((elem) => {
            if (elem instanceof NewSelectOption && this._testOption(elem.label, elem.value)) {
                shouldInclude = true;
            }
        });
        return shouldInclude;
    }

    private _onQueryUpdate() {
        const temp: any = [];

        this._listStructure.forEach((x) => {
            let expandedGroup = [];
            const arr: any = [];
            if (x instanceof NewSelectGroup) {
                expandedGroup = this._searchIteration(x, arr);
                let shouldInclude = false;
                if (expandedGroup.length > 0) {
                    expandedGroup.forEach((y: NewSelectOption) => {
                        if (this._testOption(y.label, y.value)) {
                            shouldInclude = true;
                        }
                    });
                    if (shouldInclude) {
                        temp.push(x);
                        x.hidden = false;
                    } else {
                        x.hidden = true;
                    }
                }
            } else if (x instanceof NewSelectOption && this._testOption(x.label, x.value)) {
                temp.push(x);
                x.hidden = false;
            } else {
                x.hidden = true;
            }
        });
        this._navigationStructure = [];
        this._navigationStructure = temp;

        this._updateSearchableSelect();
    }

    private _updateSearchableSelect() {
        if (this.searchable && this._input) {
            this._navigationStructure.unshift(this._input);

            this._addClearToNavigation();
        }
    }

    private _handleSearch(event) {
        this._query = event.target.value;
        this._onQueryUpdate();
    }

    private _isEllipsisActive(e) {
        const labelValue = e.target.innerText;
        this._isEllipsis = this._valueSpan.offsetWidth < this._valueSpan.scrollWidth;
        if (this._isEllipsis && labelValue && labelValue !== '') {
            this._titleValue = labelValue;
        }
    }

    private async _handleKeyDown(event: KeyboardEvent) {
        if (!this.open && !this.multiple) {
            if (event.code === 'ArrowDown') {
                event.preventDefault();
                await this._handleArrowDown();
            } else if (event.code === 'ArrowUp') {
                event.preventDefault();
                await this._handleArrowUp();
            }
        }
        if ((event.code === 'Enter' || event.code === 'Space') && !this.open) {
            event.preventDefault();
            this._handleToggle();
        } else if (this.open) {
            if (event.code === 'ArrowDown') {
                event.preventDefault();
                await this._handleOpenArrowDown();
            } else if (event.code === 'ArrowUp') {
                event.preventDefault();
                await this._handleOpenArrowUp();
            } else if (event.code === 'Enter' || event.code === 'Space') {
                this._handleOpenEnter(event);
            }
        }
    }

    private async _handleArrowDown() {
        let next;

        for (const [index, option] of this._navigationStructure.entries()) {
            if (option instanceof NewSelectOption && (option.selected || option.active) && option !== this._clear) {
                next = await this._handleArrowOption(option, index); // Await the async function
                break;
            } else if (option.active) {
                next = this._handleArrowGroup(option, index);
                break;
            }
        }

        if (!next) {
            next = this._navigationStructure[0];
        }
        if (next instanceof NewSelectOption) {
            if (this._isClearOption(next)) {
                next.active = true;
            } else {
                next.selected = true;
            }
            this.handleMenuOption(next, false);
        } else if (next) {
            next.active = true;
        }
    }

    private async _handleArrowOption(option, index) {
        option.selected = false;
        if (index < this._navigationStructure.length - 1) {
            return this._navigationStructure[index + 1];
        } else {
            await this._vrtualizerTo(0);
            return this._navigationStructure[0];
        }
    }

    private _handleArrowGroup(option, index) {
        option.active = false;
        if (index < this._navigationStructure.length - 1) {
            return this._navigationStructure[index + 1];
        } else {
            return this._navigationStructure[0];
        }
    }

    private async _handleArrowUp() {
        let next;
        for (const [index, option] of this._navigationStructure.entries()) {
            if (option instanceof NewSelectOption && (option.selected || option.active) && option !== this._clear) {
                option.selected = false;
                next = await this._handleNextIndex(index);
                break;
            } else if (option.active) {
                option.active = false;
                next = await this._handleNextIndex(index);
                break;
            }
        }

        if (!next) next = this._navigationStructure[this._navigationStructure.length - 1];
        if (next instanceof NewSelectOption) {
            if (this._isClearOption(next)) {
                next.active = true;
            } else {
                next.selected = true;
            }
            this.handleMenuOption(next, false);
        } else if (next) {
            next.active = true;
        }
    }

    private _handleNextIndex = async (index) => {
        if (index === 0) {
            if (this.options) {
                await this._vrtualizerTo(this._scroller.scrollHeight);
            }
            return this._navigationStructure[this._navigationStructure.length - 1];
        }

        return this._navigationStructure[index - 1];
    };

    private async _handleOpenArrowDown() {
        if (this.multiple) {
            await this._handleMultipleArrowDown();
        } else {
            await this._handleArrowDown();
        }
    }

    private async _handleOpenArrowUp() {
        if (this.multiple) {
            await this._handleMultipleArrowUp();
        } else {
            await this._handleArrowUp();
        }
    }

    private async _handleMultipleArrowDown() {
        let next: NewSelectOption;
        for (const [index, option] of this._navigationStructure.entries()) {
            if (option.active) {
                option.active = false;

                if (index < this._navigationStructure.length - 1) {
                    next = this._navigationStructure[index + 1];
                } else {
                    await this._vrtualizerTo(0);
                    next = this._navigationStructure[0];
                }
            }
        }
        if (!next!) next = this._navigationStructure[0];
        next!.active = true;
    }

    private async _handleMultipleArrowUp() {
        let next: NewSelectOption;
        for (const [index, option] of this._navigationStructure.entries()) {
            if (option.active) {
                option.active = false;
                next = await this._handleNextIndex(index); // Await the async function
            }
        }
        if (!next!) next = this._navigationStructure[this._navigationStructure.length - 1];
        next!.active = true;
    }

    private _handleToggle() {
        this.toggle();
        this._listStructure.forEach((option) => {
            if (!option.selected) {
                option.active = false;
            }
        });
    }

    private _handleOpenEnter(event) {
        if (!this._input?.active) {
            event.preventDefault();
            if (this.multiple) {
                this._handleMultipleEnter();
            } else {
                this._handleEnter();
            }
        }
    }

    private _handleEnter() {
        this._navigationStructure.forEach((option) => {
            if (option instanceof NewSelectOption) {
                if (this._isClearOption(option) && option.active) {
                    this._clearSelection();
                } else if (option.selected) {
                    this.toggle();
                }
            } else if (option.active) {
                if (option.back) {
                    this._fadeOut();
                } else {
                    this._navigate(option);
                }
            }
        });
    }

    private _handleMultipleEnter() {
        this._navigationStructure.forEach((option) => {
            if (option.active) {
                if (option instanceof NewSelectOption) {
                    if (this._isClearOption(option)) {
                        this._clearSelection();
                    } else {
                        option.selected = true;
                        this.handleMenuOption(option);
                    }
                } else if (option.back) {
                    this._fadeOut();
                } else {
                    this._navigate(option);
                }
            }
        });
    }

    private async _vrtualizerTo(scrollTop) {
        this._scroller.scrollTop = scrollTop;
        await this.updateComplete;
        await new Promise<void>((resolve) => {
            requestIdleCallback(() => resolve());
        });
        this._updateNavigationStructure(undefined, true);
    }
}
