import { CSSResultArray, cssSelector, cssVariableAsVar, GenerateStyles, unsafeCSS } from '@umetrics/sartorius-ui-base';

// see  https://www.sartorius.com/brand-platform/applications/digital#accordion-652930-653006
export const selectGroupStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    const white = cssVariableAsVar('color_primary_2');
    const gray = cssVariableAsVar('color_secondary_2');
    const hoverGray = '#0000001A';
    const black = cssVariableAsVar('color_primary_3');
    const selected = cssVariableAsVar('color_secondary_10');
    const activeGray = '#00000033';

    return [
        unsafeCSS(`
            :host {
                outline: 0;
            }

            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            ${root} {
                color: ${black};
                cursor: pointer;
                padding: 10px 15px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                background: ${white};
                outline: none;
            }

            ${root}:hover,
            ${is('active')} {
                background: ${hoverGray};
                color: ${black};
            }

            ${root}.open {
                background: ${gray};
                color: ${black};
                position: relative;
            }

            ${root}.selected {
                background: var(--select-option-selected-background-color, ${selected});
                color: ${white};
            }
            ${child('back')} {
                display: flex;
                align-items: center;
                justify-content: flex-start;
                flex-direction: row;
            }
            ${child('back-icon')} {
                font-size: 14px;
                margin-right: 5px;
            }
            
            ${root}:active,
            ${root}.selected:active {
                transition: background 0.2s;
                background: ${activeGray};
                color: ${black};
            }
            
            ${child('wrapper')} {
                display: none;
                animation: close 0.3s linear;
            }
            
            ${root}.open ${child('container')},
            ${root}.open:focus ${child('container')} {
                overflow: hidden;
            }
        
            ${root}.open ${child('wrapper')} {
                display: block; 
                position: absolute;
                left: 0;
                top: 0;
                box-shadow: 0 0 0 1px black;
                width: 100%;
            }
        `),
    ] as CSSResultArray;
};
