import Fuse from 'fuse.js';

const stringMatching = (candidateMatchWords: string[], candidateList: string[], threshold: number) => {
    if (candidateMatchWords.length === 0) {
        return undefined;
    }
    const toSelect = candidateMatchWords[0];

    const fuse = new Fuse(candidateList, {
        includeScore: true,
        threshold: threshold,
    });

    const bestMatchResult = fuse.search(toSelect);
    if (bestMatchResult.length === 0) {
        return stringMatching(candidateMatchWords.slice(1), candidateList, threshold);
    }

    const bestMatch = bestMatchResult[0].item;
    const bestMatchOriginalCasingIndex = candidateList.indexOf(bestMatch);
    const confidenceScore = bestMatchResult[0].score ? 1 - bestMatchResult[0].score : 0; // Convert score to confidence

    const results = { bestMatchIndex: bestMatchOriginalCasingIndex, confidenceScore: confidenceScore };

    return confidenceScore > threshold
        ? results
        : stringMatching(candidateMatchWords.slice(1), candidateList, threshold);
};

const chooseBestMatch = (
    subStringMatch: { bestMatch: string; confidenceScore: number } | undefined,
    stringMatch: { bestMatch: string; confidenceScore: number } | undefined,
    threshold: number
) => {
    if (!stringMatch) {
        return subStringMatch && subStringMatch.confidenceScore > threshold ? subStringMatch : undefined;
    }
    if (!subStringMatch) {
        return stringMatch.confidenceScore > threshold ? stringMatch : undefined;
    }

    return subStringMatch.confidenceScore > stringMatch.confidenceScore ? subStringMatch : stringMatch;
};

export const autoSelect = (candidateMatchWords: string[], candidateList: string[], threshold = 0.5) => {
    if (candidateMatchWords.length === 0 || candidateList.length === 0) {
        return undefined;
    }
    const lowerCaseCandidateMatchWords = candidateMatchWords.map((w) => w.toLowerCase());
    const lowerCaseCandidateList = candidateList.map((candidate) => candidate.toLowerCase());

    const substringMatches = lowerCaseCandidateList.filter((candidate) =>
        lowerCaseCandidateMatchWords.some((substring) => candidate.includes(substring))
    );

    const firstValidSubStringMatch = substringMatches.find((match: string) => match.length > 2);
    const candidateListIndex = firstValidSubStringMatch && lowerCaseCandidateList.indexOf(firstValidSubStringMatch);
    const bestMatchOriginalCasing = candidateListIndex ? candidateList[candidateListIndex] : undefined;
    const subStringMatchingResult =
        bestMatchOriginalCasing !== undefined
            ? {
                  bestMatch: bestMatchOriginalCasing,
                  confidenceScore: 1 / substringMatches.length,
              }
            : undefined;
    if (subStringMatchingResult && subStringMatchingResult.confidenceScore === 1) {
        return subStringMatchingResult;
    }
    const stringMatchingResultLowerCase = stringMatching(
        lowerCaseCandidateMatchWords,
        lowerCaseCandidateList,
        threshold
    );
    const stringMatchingResult = stringMatchingResultLowerCase && {
        bestMatch: candidateList[stringMatchingResultLowerCase.bestMatchIndex],
        confidenceScore: stringMatchingResultLowerCase.confidenceScore,
    };

    return chooseBestMatch(subStringMatchingResult, stringMatchingResult, threshold);
};
