import { CSSResultArray, cssSelector, cssVariableAsVar, GenerateStyles, unsafeCSS } from '@umetrics/sartorius-ui-base';

// see  https://www.sartorius.com/brand-platform/applications/digital#accordion-652930-653006
export const selectOptionStyles: GenerateStyles = (tagName) => {
    const { root, is, child } = cssSelector(tagName);

    const white = cssVariableAsVar('color_primary_2');
    const gray = '#0000001A';
    const darkGray = '#505050';
    const black = cssVariableAsVar('color_primary_3');
    const selected = cssVariableAsVar('color_secondary_10');
    const activeGray = '#00000033';

    return [
        unsafeCSS(`
            :host {
                outline: 0;
            }

            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }
            
            ${root} {
                color: var(--select-option-font-color, ${black});
                position: relative;
                padding: var(--select-options-padding, 10px 16px);
                display: flex;
                align-items: center;
                opacity: 1;
                background: ${white};
                outline: none;
                transition: background 0.2s;
                line-height: 16px;
            }
    
            ${root}.selected {
                background: var(--select-option-selected-background-color, ${selected});
                color: ${white};
            }

            ${root}:hover,
            ${is('active')} {
                transition: background 0.2s;
                background: ${gray};
                color: ${black};
            }
            ${is('disabled')} {
                pointer-events: none;
                cursor: not-allowed !important;
                color: ${cssVariableAsVar('color_secondary_7')};
            }

            ::slotted([slot=icon-before]) {
                margin-right: 5px;
                font-size: 16px;

            }
            ::slotted([slot=icon-after]) {
                margin-left: 12px;
                margin-right: 9px;
                font-size: 16px;
            }

            ${root}.selected:hover,
            ${is('active')}.selected {
                background: ${darkGray};
                color: ${white};
            }

            ${child('value')} {
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
            }
            
            ${root}:active,
            ${root}.selected:active {
                transition: background 0.2s;
                background: ${activeGray};
                color: ${black};
            }
            
        `),
    ] as CSSResultArray;
};
