import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
    state,
} from '@umetrics/sartorius-ui-base';
import { NewSelectGroupProperties } from './newSelectGroupProperties';
import { selectGroupStyles } from './newSelectGroupStyle';
import { NewSelectGroupEvent } from './newSelectGroupEvent';
import { version } from './metadata';
import back from '@umetrics/sartorius-ui-icon/dist/icons/back';
import next from '@umetrics/sartorius-ui-icon/dist/icons/next';

@defineElement({
    tagName: 'new-select-group',
    styles: selectGroupStyles,
    version,
    //    sub: [Icon]
})
export class NewSelectGroup extends BaseElement implements NewSelectGroupProperties {
    // Static members
    private static Icon;

    // Private members

    @property({ type: 'string' })
    public value;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    public open;

    // Parent decide who is selected
    @property({ type: Boolean, converter: booleanConverter })
    public get selected() {
        return this._selected;
    }
    public set selected(val) {
        if (val === this._selected) return;
        if (val === false) this.removeAttribute('selected');
        else {
            this.setAttribute('selected', 'true');
        }
        this._selected = val;
        this.requestUpdate();
        this._dispatchEvent(NewSelectGroupEvent.NewNavigate);
    }

    @property({ type: 'boolean', reflect: true, converter: booleanConverter })
    public active;

    @property({ type: Boolean, converter: booleanConverter })
    public disabled;

    @state()
    private _selected;

    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    private back;

    focus(options?: FocusOptions) {
        const head = this.shadowRoot!.querySelector('[class*="-head"]');
        (head as HTMLDivElement).focus(options);
    }
    blur() {
        const head = this.shadowRoot!.querySelector('[class*="-head"]');
        (head as HTMLDivElement).blur();
    }

    // Protected members

    // Public members
    connectedCallback() {
        this.style.setProperty('display', 'none');
        const imports: any[] = [];
        imports.push({ module: () => import('@umetrics/sartorius-ui-icon'), elem: ['Icon'] });

        NewSelectGroup.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.style.removeProperty('display');
        });
        this.dispatchEvent(
            new CustomEvent('added', {
                detail: {
                    newState: this.open,
                    prevState: !this.open,
                    data: {
                        value: this.value,
                    },
                },
                bubbles: true,
                composed: true,
            })
        );
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            active: !!this.active,
        });

        if (this.back) {
            return html`
                <div class="${rootClasses}" @mouseup="${this._backHandler}">
                <div class="${baseClassName}-back">
                <${NewSelectGroup.Icon.tag} class="${baseClassName}-back-icon"
                    size="normal"
                    icon=${back}
                    >
                </${NewSelectGroup.Icon.tag}>
                        ${this.value}</div>
        </div>
            `;
        }

        const slot = this.shadowRoot?.querySelector('slot');
        if (slot && slot.assignedNodes() && this.open) return html`<slot></slot>`;
        else {
            return html`
                <div class="${rootClasses} ${this.open ? 'open' : ''} ${this.selected ? 'selected' : ''}"
                    @mouseup=${this.disabled ? null : this._clickHandler}
                    value="${this.value}"
                >
                    <div class="${baseClassName}-label">
                        ${this.value}
                    </div>
                    <${NewSelectGroup.Icon.tag} class="${baseClassName}-icon" size="normal" icon=${next}></${
                        NewSelectGroup.Icon.tag
                    }>
                    <div class="${baseClassName}-wrapper">
                        <div class="${baseClassName}-container">
                            <slot></slot>
                        </div>
                    </div>
                </div>
            `;
        }
    }

    protected _clickHandler(e) {
        e.preventDefault();
        if (!this.disabled) {
            this._dispatchEvent(NewSelectGroupEvent.NewNavigate);
        }
    }

    protected _backHandler(e) {
        e.preventDefault();
        if (!this.disabled) {
            this._dispatchEvent(NewSelectGroupEvent.NewNavigateBack);
        }
    }

    private _dispatchEvent(event) {
        this.dispatchEvent(
            new CustomEvent(event, {
                detail: {
                    option: this,
                },
                bubbles: true,
                composed: true,
                cancelable: true,
            })
        );
    }
}
